trs:{"titulo":"Fuentes de datos."}},[_c('p',[_vm._v(" Las fuentes de datos en el ecosistema del Big Data son tan diversas como los datos mismos. Se pueden categorizar en tres grandes grupos: fuentes públicas, privadas y mixtas. ")]),_c('p',[_vm._v(" Las fuentes de datos públicos son un tesoro de información accesible para cualquiera. Gobiernos de todo el mundo están adoptando políticas de datos abiertos, publicando conjuntos de datos sobre una amplia gama de temas, desde estadísticas demográficas hasta datos meteorológicos y ambientales. Organizaciones internacionales como las Naciones Unidas y el Banco Mundial también proporcionan vastos repositorios de datos globales. Además, muchas instituciones académicas y de investigación comparten sus datos para fomentar la colaboración y el avance científico. ")]),_c('p',[_vm._v(" Las redes sociales y plataformas en línea son otra fuente rica de datos públicos. Twitter, por ejemplo, ofrece acceso a su API, permitiendo a los investigadores y analistas estudiar tendencias y opiniones públicas en tiempo real. Sitios como Wikipedia no solo proporcionan contenido enciclopédico, sino también datos sobre patrones de edición y contribución de usuarios. ")]),_c('p',[_vm._v(" En el otro extremo del espectro están las fuentes de datos privados. Estos son los datos generados y mantenidos por empresas y organizaciones para su uso interno. Registros de clientes, datos de transacciones, información de empleados y datos operativos son ejemplos de datos privados. Estos datos suelen ser altamente valiosos y sensibles, y su acceso está estrictamente controlado. ")]),_c('p',[_vm._v(" Las empresas utilizan sus datos privados para impulsar la toma de decisiones, mejorar la eficiencia operativa y obtener ventajas competitivas. Sin embargo, el manejo de datos privados conlleva grandes responsabilidades, especialmente en lo que respecta a la privacidad y la seguridad. Regulaciones como el GDPR en Europa y el CCPA en California han establecido estrictos requisitos para el manejo de datos personales. ")]),_c('p',[_vm._v(" Las fuentes mixtas representan un área interesante donde los límites entre lo público y lo privado se difuminan. Un ejemplo son los datos generados por dispositivos IoT (Internet de las Cosas). Un termostato inteligente en un hogar genera datos privados sobre el uso de energía de una familia, pero cuando estos datos se agregan a nivel de ciudad o región, pueden convertirse en una valiosa fuente de información pública sobre patrones de consumo energético. ")]),_c('p',[_vm._v(" Otro ejemplo de fuente mixta son las plataformas de crowdsourcing y ciencia ciudadana. Proyectos como eBird, donde aficionados a las aves reportan sus avistamientos, crean conjuntos de datos que son a la vez personales y públicos, y que han demostrado ser invaluables para la investigación científica. ")])])])],1)]),_c('Separador'),_vm._m(10),_vm._m(11),_vm._m(12),_c('div',{staticClass:"bloque-texto-g bloque-texto-g--inverso color-acento-botones p-3 p-sm-4 p-md-5 mb-5"},[_c('div',{staticClass:"bloque-texto-g__img",style:({
